@keyframes shadow-inset-center {
  0% {
    box-shadow: inset 0 0 0 0 transparent;
  }
  to {
    box-shadow: inset 0 0 14px 0 rgb(0 0 0/50%);
  }
}
.rectangle-div {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-8xs);
  background-color: var(--color-gainsboro);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  width: 149px;
  height: 44px;
  cursor: pointer;
}
.div14 {
  position: absolute;
  top: 5px;
  left: 42.5px;
  font-size: var(--font-size-13xl);
  font-family: var(--font-inter);
  color: var(--color-black);
  text-align: left;
}
.button5,
.child1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 149px;
  height: 44px;
}
.button5 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
}
.button5:hover {
  animation: 1s ease 0s infinite normal none shadow-inset-center;
  opacity: 1;
}
.child1 {
  border-radius: var(--br-8xs);
  background-color: var(--color-gainsboro);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
}
.div15,
.div16,
.div17 {
  position: absolute;
  top: 5px;
  left: 42.5px;
}
.div15,
.div17 {
  top: 57px;
  left: 0;
  width: 149px;
  height: 44px;
}
.div17 {
  top: 113px;
}
.div19,
.div20,
.parent {
  position: absolute;
  top: 8px;
  left: 42.5px;
}
.div19,
.parent {
  top: 168px;
  left: 0;
  width: 149px;
  height: 47px;
}
.parent {
  top: 9px;
  left: 13px;
  height: 201px;
  overflow-y: auto;
}
.header3 {
  position: relative;
  background-color: var(--color-black);
  width: 100%;
  height: 215px;
  overflow: hidden;
  cursor: pointer;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-inter);
}
